@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*,*::after,*::before{margin:0;padding:0;box-sizing:inherit;font-family:inherit;transition:all 0.3s;outline:none}html{font-size:62.5%}body{box-sizing:border-box}ul{list-style:none}body{font-family:'Rubik', sans-serif;font-size:2rem;font-weight:300}h1{margin:1.2rem;font-size:4rem;color:#8a9ecc}h2{margin:1rem;font-size:2.3rem}.Todo{display:flex;justify-content:space-between;align-content:center;margin-top:1rem}.Todo__text{cursor:pointer;text-align:left;word-break:break-all;margin-right:auto}.Todo__text-done{text-decoration:line-through;color:grey}.TodoForm__form{display:flex;justify-content:space-between;margin-bottom:3rem}input{background-color:white;border:1px solid transparent;border-radius:2rem;padding:1rem;font-family:inherit;font-size:inherit;width:100%}input:focus{border:1px solid #8a9ecc}.TodoList{margin:2rem auto;max-width:400px;background-color:rgba(138,158,204,0.1);border:1px solid #8a9ecc;border-radius:3px;padding:2rem}button{background-color:white;border:1px solid transparent;border-radius:2rem;align-self:center;margin-left:1rem;padding:1rem;cursor:pointer}button:hover,button:active{border:1px solid #8a9ecc}

